import React, { useState } from "react"

const ChipPool = props => {
  let pool = props.game.table.pool
  let currentPlayer = props.game.getCurrentPlayer()
  let mode = props.mode
  let extraDisabled = props.extraDisabled

  let [active, setActive] = useState(false)
  let [chipsLeft, setChipsLeft] = useState(pool.getChips(props.gem))

  let onChipClick = e => {
    e.preventDefault()
    if (active === false) {
      setActive(true)
      props.add(props.gem)
    } else {
      setActive(false)
      props.remove(props.gem)
    }
  }

  let isDisabled = () => {
    if (extraDisabled(props.gem)) return true
    if (mode === 2) {
      if (pool.canTakeTwoChips(props.gem)) {
        return false
      }
    }
    if (mode === 1) {
      if (pool.canTakeChip(props.gem)) {
        return false
      }
    }
    return true
  }

  return (
    <tr><div className="chippool">
      <button
        className={
          "chippool-btn" + " "
          + props.gem + "-pool" + " "
          + (active ? "chippool-active": "") + " "
        }
        onClick={onChipClick}
        disabled={isDisabled()}>
        {chipsLeft}
      </button>
    </div></tr>
  )
}

export default ChipPool
