import React from 'react';
import Card from "./card"

const Level = (props) => {
  let cards = props.cards

  let cardlist = []
  for (let card of cards) {
    cardlist.push(<td><Card card={card} player={props.game.getCurrentPlayer()}/></td>)
  }

  return (
    <tr class="cardlist">{cardlist}</tr>
  )
}

export default Level;
