import React, { useState } from "react"
import { Link, useParams } from "react-router-dom"
import Game from "../logic/game"
import Level from "./level"
import ChipPool from "./chip-pool"

import "./game-styles.css"

function useRefresh() {
  let [currentRefresh, setRefresh] = useState(true)
  return () => {
    setRefresh( !currentRefresh )
  }
}

const GameComponent = props => {
  let players = parseInt(useParams().players)

  let playerNames = []
  for (let i of [...Array(players).keys()]) {
    playerNames.push("Player " + (i + 1))
  }

  let [mode, setMode] = useState(1)
  let [game, setGame] = useState(new Game(playerNames).init())
  let [activeChips, setActiveChips] = useState([])
  let refresh = useRefresh()

  let takeChips = (e) => {
    e.preventDefault()
    let player = game.getCurrentPlayer()
  }


  const addActiveChip = (gem) => {
    game.addActiveChip(gem)
    setActiveChips(game.getActiveChips())
    refresh()
    console.log(activeChips)
  }
  const removeActiveChip = (gem) => {
    game.removeActiveChip(gem)
    setActiveChips(game.getActiveChips())
    refresh()
    console.log(activeChips)
  }

  const extraDisabled = (gem) => {
    if (mode === 1) {
      if (activeChips.length == 3) {
        if (!activeChips.includes(gem)) {
          return true
        }
      }
      if (activeChips[0] == "gold") {
        if (gem !== "gold") {
          return true
        }
      }
    }
    if (mode === 2) {
      if (gem === "gold") {
        return true
      }
      if (activeChips.length === 1) {
        if (activeChips[0] !== gem) {
          return true
        }
      }
    }
    return false
  }


  let pools = []
  for (let pool of ["diamond", "sapphire", "emerald", "ruby", "opal", "gold"]) {
    pools.push(<ChipPool mode={mode} gem={pool} game={game} add={addActiveChip} remove={removeActiveChip} extraDisabled={extraDisabled}/>)
  }

  const changeMode = (e) => {
    e.preventDefault()
    if (mode === 1) {
      setMode(2)
    } else {
      setMode(1)
    }
  }

  return (
    <div>
    <table id="table"><tr>
    <td><table id="cards-table">
      <Level cards={game.table.cards[3]} game={game} />
      <Level cards={game.table.cards[2]} game={game} />
      <Level cards={game.table.cards[1]} game={game} />
    </table></td>
  <td><table id="chips-table">
    <tr><button id="mode-btn" onClick={changeMode}>Take {mode}</button></tr>
    <tr><button id="take-chips" onClick={takeChips}>Take Chips</button></tr>
    {pools}
    </table></td>
  </tr></table>
    </div>
  )
}

export default GameComponent
